.form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    margin: 20px 0;
    padding: 10px 30px;
    border: 1px solid black;
    border-radius: 10px;
}

.label {
    font-size: 20px;
    font-weight: 500;
}

.input {
    padding: 10px 30px;
    border: 1px solid black;
    border-radius: 10px;
    font-size: 16px;
    font-weight: 600;
}

.button {
    padding: 10px 30px;
    border: 1px solid black;
    border-radius: 10px;
    box-shadow: 2px 2px 4px gray;
    font-size: 16px;
    font-weight: 600;
    text-transform: capitalize;
}

.button:hover,
.button:focus,
.btndel:hover,
.btndel:focus {
    background-color: yellow;
    cursor: pointer;
}

.button:active,
.btndel:active {
    background-color: rgb(143, 143, 207);
    color: white;
}