.list {
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 10px 0;
    gap: 10px;
    font-size: 20px;
    font-weight: 500;
}

.btndel {
    margin-left: 10px;
    padding: 5px 15px;
    border: 1px solid black;
    border-radius: 10px;
    box-shadow: 2px 2px 4px gray;
    font-size: 16px;
    font-weight: 600;
    text-transform: capitalize;
    cursor: pointer;
}